import React, { useContext, useEffect, useState } from 'react';
import Fade from 'react-reveal/Fade';
import Tilt from 'react-tilt';
import { Container, Row, Col } from 'react-bootstrap';
import PortfolioContext from '../../context/context';
import Title from '../Title/Title';
import Image from '../Core/Image';
import Cringeometer from './../Core/Cringeometer'

const Games = () => {
  const { games } = useContext(PortfolioContext);

  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="games">
      <Container>
        <div className="games-wrapper">
          <Title title="Games" />
          {games.map((game) => {
            const { title, info, info2, url, img, id, linkCaption, year, cringe} = game;

            return (
              <Row key={id}>
                <Col lg={4} sm={12}>
                  <Fade
                    left={isDesktop}
                    bottom={isMobile}
                    duration={1000}
                    delay={500}
                    distance="30px"
                  >
                    <div className="games-wrapper__text">
                      <h3 className="games-wrapper__text-title">{title}</h3>
                      <div>
                        <p>
                          {info}
                        </p>
                        <p className="mb-4">{info2 || ''}</p>
                        <p className="mb-4">{year}</p>
                      </div>
                      {url ?
                      <a
                        target="_blank"
                        rel="noopener noreferrer"
                        className="cta-btn cta-btn--hero"
                        href={url || '#!'}
                      >
                        {linkCaption}
                      </a>
                      :<></>}
                    </div>
                  </Fade>
                  {cringe &&
                    <Fade
                    left={isDesktop}
                    bottom={isMobile}
                    duration={1000}
                    delay={100}
                    distance="30px"
                  >
                    <Cringeometer id={`cringeometer_${id}`} percent={cringe} />
                  </Fade>
                 }
                </Col>
                <Col lg={8} sm={12}>
                  <Fade
                    right={isDesktop}
                    bottom={isMobile}
                    duration={1000}
                    delay={1000}
                    distance="30px"
                  >
                    <div className="games-wrapper__image">
                      <a
                        href={url || '#!'}
                        target="_blank"
                        aria-label="Games Link"
                        rel="noopener noreferrer"
                      >
                        <Tilt
                          options={{
                            reverse: false,
                            max: 8,
                            perspective: 1000,
                            scale: 1,
                            speed: 300,
                            transition: true,
                            axis: null,
                            reset: true,
                            easing: 'cubic-bezier(.03,.98,.52,.99)',
                          }}
                        >
                          <div data-tilt className="thumbnail rounded">
                            <Image alt={title} filename={img} />
                          </div>
                        </Tilt>
                      </a>
                    </div>
                  </Fade>
                </Col>
              </Row>
            );
          })}
        </div>
      </Container>
    </section>
  );
};

export default Games;
